/* fonts */
@font-face {
  font-family: 'Quicksand';
  src: url('/Quicksand-Light.woff2') format('woff2'),
      url('/Quicksand-Light.woff') format('woff');
  font-weight: 300;
  font-style: normal;
}

/* reset & common styles */
body, ul, h1, h2, h3, p, button, a, div{
  margin: 0;
  padding: 0;
  color: white;
  font-family: 'Quicksand';
  letter-spacing: 2px;
  list-style-type: none;
  text-decoration: none;
}
body{
  background: rgb(123, 0, 0);
  background: radial-gradient(circle, rgb(123, 0, 0) 0%, rgb(86, 20, 20) 100%);
  overflow: hidden;
}
button{
  color: white;
  padding: 10px 30px;
  font-size: 1em;
  background: transparent;
  border-radius: 50px;
  border: 1px solid white;
  margin: 40px auto 0;
  cursor: pointer;
  opacity: 0.7;
}
.container{
  max-width: 300px;
  margin: 50px auto 40px;
}

.container h2{
  margin-bottom: 50px;
}

.container h3{
  padding-bottom: 10px;
  margin-bottom: 10px;
  border-bottom: 1px solid rgba(255,255,255,0.2);
}
.container li{
  padding: 10px;
  cursor: pointer;
}

.container input{
  width: 40px;
  height: 100%;
  outline: none;
  border: none;
  font-weight: 700;
  color: white;
  font-size: 16px;
  border-radius: 3px;
  padding: 1px 1px 1px 5px;
  background: rgba(255, 255, 255, 0.3);
}

.container input::placeholder{
  font-weight: 300;
}

.container input:focus{
  outline: none;
  border: none;
}

/* header */
header{
  display: flex;
  padding: 40px;
  align-items: center;
}
header .title{
  flex: 1;
  margin-left: 20px;
  padding-bottom: 10px;
  border-bottom: 1px solid rgba(255,255,255,0.2);
}
header h1{
  font-size: 1.5em;
  cursor: pointer;
  display: inline-block;
}
header .pizza-svg {
  width: 80px;
  overflow: visible;
  stroke: #fff;
  stroke-width: 4;
  stroke-linejoin: round;
  stroke-linecap: round;
  cursor: pointer;
}

/* lists */
li{
  display: flex;
  justify-content: space-between;
}
li .li-div-name-and-price-item{
  flex: 1;
  margin-right: 10px;
  display: flex;
  justify-content: space-between;
}

li span.active{
  font-weight: bold;
}
li span.active-title{
  font-weight: bold;
}
li span.active-title::before{
  content: '>';
  position: relative;
  top: -2px;
  margin-right: 6px;
  transform: scale(0.8, 1);
  display: inline-block;
}

/* pages */
.home h2{
  font-size: 2em;
  margin-bottom: 30px;
}
.home.container, 
.order.container{
  text-align: center;
  max-width: 800px;
}
.home button{
  color: white;
  padding: 15px 30px;
  font-size: 1.6em;
  border: 3px solid white;
  margin: 30px 20px;
}

.toppings a{
  margin-right: 5px;
}

.order-div-items{
  margin-bottom: 20px;
}

.order-title-items{
  font-weight: 700;
  font-size: 1.2em;
  margin: 0 0 10px;
}

.total-text{
  font-weight: 700;
  font-size: 1.5em;
}

/* modal */
.backdrop{
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  z-index: 1
}

.modal{
  max-width: 400px;
  margin: 0 auto;
  padding: 40px 20px;
  background: white;
  border-radius: 10px;
  text-align: center;
}

.modal div button{
  color: #444;
  border-color: #444;
  font-weight: bold;
  margin-top: 20px;
  width: 150px;
}

.modal .btns-modal{
  display: flex;
  justify-content: center;
  gap: 5px;
}

.modal p{
  color: #444;
  font-weight: bold;
}

/* loader */
.loader{
  width: 10px;
  height: 10px;
  margin: 40px auto;
  border-radius: 50%;
  background: #fff;
}